import Vue from 'vue'
import App from './App.vue'
import router from './router/router'

import './ults/axios.js'
import Time from './ults/time'

Vue.config.productionTip = false
Vue.prototype.$Time = Time;

import store from './ults/vuex.js'
import './ults/element-ui.js'
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
